@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  overflow: scroll;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  border-radius: 20px; /* Remove scrollbar space */
  background: black; /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #7e89fd;
  height: 0px;
  width: 0px;
}

body {
  background-color: #121418;
}
.landing-container {
  height: 100%;
  width: 100%;
}
.landing-background-primary {
  background-color: #7e89fd;
  height: 50vh;
  width: 100%;
  border-radius: 5rem 5rem 0 0;
}
.heading {
  font-weight: 800;
  color: #7e89fd;
}
.sub-heading {
  color: white;
}
.input {
  flex: 0.9 1;
}
.button {
  margin-right: 1rem !important;
  margin-bottom: 1rem !important;
}
.dialogue-container {
  background-color: white;
  cursor: default;
  width: 100%;
  height: 65vh;
  overflow-y: scroll;
  padding: 0rem 2rem 1rem 2rem;
  border-radius: 50px;
  margin-top: 0.5rem;
}
.header {
  background-color: #7e89fd;
  color: white;
  padding: 1rem 2rem;
  box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
  z-index: 12;
  margin-top: 1.5rem;
}
.main-container {
  width: 100%;
}
.dialogue {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0.6rem 0.2rem;
  padding: 0.5rem 0.75rem;
  display: inline-block;
  border-radius: 0.5rem;
  box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
  min-width: 50px;
}
.d-human {
  background-color: #7e89fd;
  color: white;
}
.d-bot {
  color: #7e89fd;
  background-color: #121418;
  border: 2px solid #7e89fd;
}
.modal {
  background-color: white;
  height: 75vh;
  width: 80vw;
}
.msg-author {
  font-size: 0.75rem;
}
.msg-content {
  font-size: 1.25rem;
}
.MuiOutlinedInput-root {
  border-radius: 50px !important;
}
.MuiTypography-h4 {
  color: #fff;
}
.MuiTypography-body1 {
  color: #fff;
}

.chat_infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #7e89fd;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}

.chat_leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.chat_rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 5%;
}

.chat_onlineIcon {
  margin-right: 5%;
}

.chat_form {
  display: flex;
  border-top: 2px solid #d3d3d3;
}

.chat_input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 90%;
  font-size: 1.2em;
}

.chat_sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979ff;
  padding: 20px;
  display: inline-block;
  border: none;
  width: 20%;
}

.chat_messageBox {
  background: #f3f3f3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.chat_messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.chat_messageText img {
  vertical-align: middle;
}

.chat_messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.chat_sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
}

.chat_pl-10 {
  padding-left: 10px;
}

.chat_pr-10 {
  padding-right: 10px;
}

.chat_justifyStart {
  justify-content: flex-start;
}

.chat_justifyEnd {
  justify-content: flex-end;
}

.chat_colorWhite {
  color: white;
}

.chat_colorDark {
  color: #353535;
}

.chat_backgroundBlue {
  background: #7e89fd;
}

.chat_backgroundLight {
  background: #f3f3f3;
}

.chat_messages {
  padding: 5% 5%;
  overflow: auto;
  flex: auto;
}
.chat_outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 64px);
}

.chat_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #202225;
  border-radius: 8px;
  height: 80%;
  width: 50%;
  box-shadow: 10px 10px 50px rgba(0, 0, 0, 0.16);
}

@media (min-width: 320px) and (max-width: 480px) {
  .chat_outerContainer {
    height: 100%;
  }

  .chat_container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .chat_container {
    width: 60%;
  }
}

.chat_joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: calc(100vh - 8rem - 64px);
  /* background-color: #7e89fd25; */
  padding: 4rem 0;
}

.chat_joinInput {
  border-radius: 0;
  padding: 15px 20px;
  width: 100%;
}

.chat_heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
}

.chat_button {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979ff;
  padding: 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  width: 100%;
}

.chat_mt-20 {
  margin-top: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .chat_joinOuterContainer {
    height: 100%;
  }

  .chat_joinInnerContainer {
    width: 90%;
  }
}

